<template>
  <div class="user" v-if="userInfo">
    <base-panel>
      <template #panel-head>
        <router-link to="/">主页/</router-link>
      </template>
      <template #panel-content>
        <div class="userInfo" v-if="userInfo">
          <img class="avatar" :src="userInfo.avatar_url" alt="">
          <span>{{userInfo.loginname}}</span>
        </div>
        <div class="user-about" v-if="userInfo">
          <p>{{userInfo.score}} 积分</p>
          <p><span class="iconfont icon-github-fill"></span> @{{userInfo.githubUsername}}</p>
          <p class="create">注册时间 {{userInfo.create_at | fromNow}}</p>
        </div>
      </template>
    </base-panel>
    <base-panel>
      <template #panel-head>
        最近创建的话题
      </template>
      <template #panel-content v-if="userInfo">
        <TopicItem :isUser="true" v-for="item in userInfo.recent_topics" :key="item.id" v-bind="item"/>
      </template>
    </base-panel>
    <base-panel>
      <template #panel-head>
        最近参与的话题
      </template>
      <template #panel-content v-if="userInfo">
        <TopicItem :isUser="true" v-for="item in userInfo.recent_replies" :key="item.id" v-bind="item"/>
      </template>
    </base-panel>
  </div>
</template>

<script>
import BasePanel from '../components/layout/BasePanel.vue'
import TopicItem from '../components/TopicItem.vue'
import {getUserInfo} from '../plugins/api'
export default {
  components: { BasePanel, TopicItem },
  props:['loginname'],
  data(){
    return {
      userInfo: null
    }
  },
  watch:{
    loginname:{
      async handler(val){
        const res = await getUserInfo(val)
        this.userInfo = res.data
      },
      immediate: true
    }
  },
  // async created(){
  //   const res = await getUserInfo(this.loginname)
  //   // console.log(res)
  //   this.userInfo = res.data
  // }

}
</script>

<style lang='less'>
a{
  color: #80bd01;
}
.userInfo{
  display: flex;
  margin-bottom: 10px;
  .avatar{
  width: 40px;
  border-radius: 5px;
  margin-left: 15px;
 }
 span{
  margin-left: 10px;
  color: #666;
  font-size: 14px;
 }
}
.user-about{
  p{
    font-size: 14px;
    margin-left: 15px;
  }
  .create{
  margin-left: 15px;
  color: #666;
  padding-bottom: 15px;
}
}
</style>